<template>
    <Form ref="formData" :model="formData" :rules="ruleValidate" :label-width="80">
        <FormItem label="退货规则名称" prop="name" style="width:400px">
            <Input v-model="formData.name" placeholder="请输入退货规则名,名称是唯一的"/>
        </FormItem>
        <FormItem label="产品类型" prop="productType" style="width:400px" >
            <Select v-model="formData.productType" placeholder="选择产品类型">
                <Option :value="5001">景点</Option>
                <Option :value="5002">酒店</Option>
                <Option :value="5003">线路</Option>
            </Select>
        </FormItem>
        <FormItem label="是否可退" prop="refund">
            <RadioGroup v-model="formData.refund" @on-change="changStatus">
                <Radio :label="1">是</Radio>
                <Radio :label="0">否</Radio>
            </RadioGroup>
        </FormItem>
        <FormItem v-if="formData.refund===1" label="是否无损退" prop="free">
            <RadioGroup v-model="formData.free">
                <Radio :label="1">是</Radio>
                <Radio :label="0">否</Radio>
            </RadioGroup>
        </FormItem>
        <FormItem v-if="formData.free ===0 && formData.refund===1" label="开始游玩前">
            <Row>
                <i-col span="3" style="text-align: center">
                    <InputNumber :max="10" :min="1" v-model="rule[0].before.day"></InputNumber>天
                </i-col>
                <i-col span="3">
                    <FormItem prop="date">
                        <TimePicker
                            format="HH:mm"
                            v-model="rule[0].before.time"
                            placeholder="选择时间"
                            style="width: 112px"
                        ></TimePicker>时
                    </FormItem>
                </i-col>
                <i-col span="3" style="text-align: center">扣费类型</i-col>
                <i-col span="3" style="text-align: center">
                    <Select v-model="rule[0].fixed" placeholder="扣费类型">
                        <Option :value="1">数值</Option>
                        <Option :value="0">百分比</Option>
                    </Select>
                </i-col>
                <i-col span="3" style="text-align: center">
                    <FormItem v-if="rule[0].fixed ===0 || rule[0].fixed ===1" label="扣费值">
                        <InputNumber
                            v-if="rule[0].fixed ===0"
                            :max="100"
                            :min="0"
                            v-model="rule[0].value"
                            :formatter="value => `${value}%`"
                            :parser="value => value.replace('%', '')"
                        />
                        <InputNumber v-if="rule[0].fixed ===1" v-model="rule[0].value" :min="0"/>
                    </FormItem>
                </i-col>
            </Row>
        </FormItem>
        <FormItem v-if="formData.free ===0 && formData.refund===1" label="开始游玩中">
            <Row>
                <i-col span="6" style="text-align: center">&nbsp;&nbsp;&nbsp;&nbsp;</i-col>
                <i-col span="3" style="text-align: center">扣费类型</i-col>
                <i-col span="3" style="text-align: center">
                    <Select v-model="rule[1].fixed" placeholder="扣费类型">
                        <Option :value="1">数值</Option>
                        <Option :value="0">百分比</Option>
                    </Select>
                </i-col>
                <i-col span="3" style="text-align: center">
                    <FormItem v-if="rule[1].fixed ===0 || rule[1].fixed ===1" label="扣费值">
                        <InputNumber
                            v-if="rule[1].fixed ===0"
                            :max="100"
                            :min="0"
                            v-model="rule[1].value"
                            :formatter="value => `${value}%`"
                            :parser="value => value.replace('%', '')"
                        />
                        <InputNumber v-if="rule[1].fixed ===1" v-model="rule[1].value" :min="0"/>
                    </FormItem>
                </i-col>
            </Row>
        </FormItem>
        <FormItem v-if="formData.free ===0 && formData.refund===1" label="开始游玩后">
            <Row>
                <i-col span="3" style="text-align: center">
                    <InputNumber :max="10" :min="1" v-model="rule[2].after.day"></InputNumber>天
                </i-col>
                <i-col span="3">
                    <FormItem prop="date">
                        <TimePicker
                            format="HH:mm"
                            v-model="rule[2].after.time"
                            placeholder="选择时间"
                            style="width: 112px"
                        ></TimePicker>时
                    </FormItem>
                </i-col>
                <i-col span="3" style="text-align: center">扣费类型</i-col>
                <i-col span="3" style="text-align: center">
                    <Select v-model="rule[2].fixed" placeholder="扣费类型">
                        <Option :value="1">数值</Option>
                        <Option :value="0">百分比</Option>
                    </Select>
                </i-col>
                <i-col span="3" style="text-align: center">
                    <FormItem v-if="rule[2].fixed ===0 || rule[2].fixed ===1" label="扣费值">
                        <InputNumber
                            :max="100"
                            :min="0"
                            v-if="rule[2].fixed ===0"
                            v-model="rule[2].value"
                            :formatter="value => `${value}%`"
                            :parser="value => value.replace('%', '')"
                        />
                        <InputNumber v-if="rule[2].fixed ===1" v-model="rule[2].value" :min="0"/>
                    </FormItem>
                </i-col>
            </Row>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formData')">修改</Button>
        </FormItem>
    </Form>
</template>
<script>
import bus from '@/assets/eventBus';
export default {
    name: 'RefundRuleUpdate',
    data () {
        return {
            rule: [
                {
                    before: { day: null, time: null },
                    fixed: null,
                    value: null
                },
                {
                    before: { day: null, time: null },
                    after: { day: null, time: null },
                    fixed: null,
                    value: null
                },
                {
                    after: { day: null, time: null },
                    fixed: null,
                    value: null
                }
            ],
            formData: {
                id: null,
                name: null,
                productType: null,
                refund: null,
                free: null,
                rule: null
            },
            ruleValidate: {
                name: [
                    {
                        required: true,
                        message: '请填入名字',
                        trigger: 'blur'
                    }
                ],
                productType: [
                    {
                        required: true,
                        message: '请填入产品类型'
                    }
                ]
            }
        };
    },
    watch: {
    // rule: {
    //   handler(curVal) {
    //   },
    //   deep: true,
    //   immediate: true
    // }
    },

    mounted () {
        this.formData = this.$route.params;
        if (this.formData.rule !== undefined && this.formData.rule !== null) {
            const temp = JSON.parse(this.formData.rule);
            temp.map(item => {
                if (item.fixed === 1) {
                    item.value = parseInt((item.value / 100).toFixed(0));
                }
            });
            this.rule = temp;
        }
    },
    methods: {
        async handleSubmit (name) {
            const valid = await this.$refs.formData.validate();
            if (valid) {
                if (this.formData.free === 0 && this.formData.refund === 1) {
                    const temp = JSON.parse(JSON.stringify(this.rule));
                    temp[1].before.time = temp[0].before.time;
                    temp[1].before.day = temp[0].before.day;
                    temp[1].after.time = temp[2].after.time;
                    temp[1].after.day = temp[2].after.day;
                    temp.map(item => {
                        if (item.fixed === 1) {
                            item.value = parseInt((item.value * 100).toFixed(0));
                        }
                    });
                    this.formData.rule = JSON.stringify(temp);
                }
                const res = await this.post('api/refund/rules/update', this.formData);
                if (res.success) {
                    this.$Message.info('修改退货规则成功');
                    bus.$emit('RefundRule-update');
                    this.$router.push({ name: 'RefundRuleList' });
                }
            }
        },
        changStatus () {
            if (this.formData.refund === 0) {
                this.formData.free = null;
                this.formData.rule = null;
            }
        }
    }
};
</script>
